import React from 'react';

import { Modal } from 'react-bootstrap'

import { useDispatch, useSelector } from "react-redux";
import * as Actions from 'store/actions';

import { Content, Title } from './style'
import * as Icon from "react-bootstrap-icons";

export default function ModalPolitica() {

	const _modal = useSelector(({ modal }) => modal.politica);
	const dispatch = useDispatch()

	return (
		<>
			<Modal
				show={_modal}
				size="lg"
				onHide={() => dispatch(Actions.close_modal('politica'))}
				backdrop="static"
				keyboard={true}
				centered
			>
				<Modal.Header className="topo-modal-politica">
					<Modal.Title className='row col-12 justify-content-center'>
						<Title className='row col-12 p-0 flex justify-content-between'>
							<div className='title'>POLTICA DE PRIVACIDADE BARBO EMPREENDIMENTOS</div>
							<div><a href={"#"} onClick={() => dispatch(Actions.close_modal('politica'))}
								className='text-center'><Icon.XCircle color='#252525' size={18} /></a></div>
						</Title>
					</Modal.Title>
				</Modal.Header>
				<Modal.Body>
					<Content className='row col-12 text-left'>
						<strong>SOBRE A NOSSA POLTICA DE PRIVACIDADE</strong>

						Como tratamos seus dados pessoais

						Ns, da Barbo, sabemos como  importante que voc esteja seguro sobre seus Dados Pessoais. <br/>
						Por isto todas as informaes pessoais relativas a usurios, clientes ou visitantes, que usem nossos servios digitais ou fsicos, so tratadas em concordncia com a Lei da Proteo de Dados Pessoais, alm de atender s boas prticas de Segurana da Informao.<br/><br/>

						<strong>Por que coletamos e tratamos seus Dados Pessoais?</strong><br/>

						Ao oferecermos nossos servios, precisamos coletar e tratar seus Dados Pessoais para as seguintes finalidades:<br/>

						a) prover os servios de incorporao e comercializao de imveis;<br/>
						b) atender nossos parceiros de negcio, como nos financiamentos dos imveis que comercializamos;<br/>
						c) atender s obrigaes legais e administrativas de rgos pblicos ligados a estes servios, no mbito municipal, estadual e federal.<br/><br/>

						Assim, quando realizamos qualquer atividade que utiliza Dados Pessoais, como cadastros, processamentos diversos relativos ao desenvolvimento do servio contratado, fazemos isto com muito respeito e responsabilidade e tomamos medidas tcnicas e administrativas para assegurar a sua privacidade.<br/><br/>

						Para tanto, desenvolvemos uma Poltica de Privacidade para cuidar especificamente de Dados Pessoais, como parte integrante do nosso SGPD  Sistema de Gesto de Privacidade e Dados e que descreve, entre outras coisas, como os dados so coletados, armazenados, tratados, compartilhados, retidos e descartados.<br/><br/>

						Tambm, estamos constantemente aprimorando nossa Poltica de Privacidade e, em nosso site, alm de encontrar esta poltica, voc tambm encontrar as formas para entrar em contato conosco e esclarecer quaisquer dvidas sobre Privacidade de Dados Pessoais.<br/><br/><br/>



						<strong>POLTICA DE PRIVACIDADE</strong><br/>

						Em agosto de 2018, com a publicao da LGPD  Lei Geral de Proteo de Dados, surgiram novos conceitos e definies que norteiam a forma como os Dados Pessoais devem ser tratados pelas organizaes.<br/>

						Assim, descrevemos a seguir estes conceitos e definies importantes, bem como outros detalhes da nossa Poltica de Privacidade e como adequamos a BARBO para atender s leis vigentes.<br/><br/>

						1. Entenda quem  quem perante a LGPD<br/><br/>

						1.1. Titular<br/>

						 voc, a pessoa que possui os Dados Pessoais, seja na condio de cliente BARBO, colaborador ou mesmo de um visitante, presencial ou remoto (on-line).<br/><br/>

						1.2. Controlador<br/>

						Somos ns, a BARBO, onde atravs de nossas empresas, reas e departamentos competem as decises referentes ao tratamento de Dados Pessoais.<br/><br/>

						1.3. Controlador conjunto<br/>

						So empresas homologadas pela BARBO, como por exemplo imobilirias parceiras, que podem coletar informaes adicionais para atender a um servio prestado a voc.<br/><br/>

						1.4. Operador<br/>

						Em determinadas situaes, so nossos parceiros credenciados que, em nosso nome, realizam o tratamento de Dados Pessoais.<br/><br/>

						1.4. Encarregado (DPO  Data Protection Officer)<br/>

						 a pessoa responsvel por assegurar o cumprimento da LGPD dentro de uma organizao. Na BARBO o Encarregado trabalha em conjunto com uma equipe multidisciplinar e um comit para poder conduzir os trabalhos necessrios nas reas de tecnologia, administrativa e jurdica.<br/><br/>

						1.5. ANPD<br/>

						 a Autoridade Nacional de Proteo de Dados, rgo federal institudo especialmente para zelar, auxiliar as empresas e fiscalizar o cumprimento da LGPD no Brasil.<br/><br/>

						2. Alguns conceitos e definies importantes<br/>

						(os conceitos so apresentados em ordem que vo sendo mencionados)<br/><br/>

						2.1. Poltica de Privacidade<br/>

						 o conjunto de regras, procedimentos e orientaes administrativas e de tecnologia a serem observados e seguidos por todos os envolvidos de forma a assegurar a privacidade de seus Dados Pessoais.<br/><br/>

						2.2. Dado Pessoal<br/>

						 o dado relativo a uma pessoa, sendo este capaz de identific-la ou tornar possvel a sua identificao. Como exemplo, so Dados Pessoais que permitem a sua identificao: Nome, CPF, RG, endereo, telefone, e-mail etc.<br/><br/>

						2.3. Dado Pessoal Sensvel<br/>

						 qualquer informao sobre origem racial ou tnica, convico religiosa, opinio poltica, filiao a sindicato ou a organizao de carter religioso, filosfico ou poltico, dado referente  sade ou  vida sexual, dado gentico ou biomtrico, quando vinculado a uma pessoa.<br/><br/>

						2.4. Tratamento<br/>

						 qualquer ao realizada com Dados Pessoais. Por exemplo, coleta, armazenamento, consulta, atualizao, compartilhamento, transmisso, classificao, reproduo, excluso, inativao etc.<br/><br/>

						2.5. Tratamento de Dados Pessoais de Crianas e de Adolescentes<br/>

						O tratamento de Dados Pessoais de crianas somente deve ser realizado com o consentimento especfico dado por pelo menos um dos pais ou pelo responsvel legal.<br/><br/>

						2.6. Consentimento<br/>

						 a sua declarao autorizando o tratamento de seus Dados Pessoais quando no houver uma outra base legal, como por exemplo, um contrato. Ns sempre solicitaremos o consentimento para uma finalidade especfica e voc ter total liberdade para concordar ou no com os Termos de Tratamento dos Dados Pessoais. No concordando, os Dados Pessoais no sero coletados, porm isto poder limitar alguma prestao de nossos servios ou experincias.<br/>

						Os Dados Pessoais podero ser utilizados sem o prvio consentimento do Titular ou responsvel legal nas seguintes hipteses: a) se tivermos que cumprir obrigao legal ou regulatria; b) para exercermos nossos direitos em processos judiciais e administrativos, c) para realizarmos estudos e pesquisas, assegurando a anonimizao; d) para executarmos o contrato ou procedimentos preliminares a este; e) para proteo  vida ou da segurana fsica do Titular, f) para atendermos legtimo interesse, respeitando-se os direitos e liberdades fundamentais do Titular; e g) para proteo do crdito.<br/><br/>

						2.7. Legtimo interesse<br/>

						 o que permite que ns possamos tratar Dados Pessoais para atender as necessidades operacionais, administrativas e legais, de forma a prover os servios oferecidos pela BARBO a voc. A LGPD define que os interesses s sero considerados legtimos se a forma de uso dos Dados Pessoais no for contrria  legislao brasileira, a finalidade de uso for especfica e desde que seja dada efetiva transparncia a tais usos, bem como sejam respeitados os seus direitos e liberdades individuais.<br/><br/>

						2.8. Bases legais<br/>

						So as possibilidades permitidas pela lei e que autorizam o tratamento de Dados Pessoais. Por exemplo, o consentimento  uma base legal, como tambm a necessidade de cumprir um contrato que ns temos com voc ou o cumprimento de uma outra legislao.<br/><br/>

						2.9. Plataformas<br/>

						So os sites, sistemas e aplicativos com funes especficas e que podem coletar e tratar dados diversos, incluindo Dados Pessoais.<br/><br/>

						2.10. Servios e Experincias<br/>

						Servios so as atividades que realizamos para voc. Os servios podem ser presenciais ou remotos, digitais ou no digitais. Experincias so as formas como voc se relaciona ou interage com a gente.<br/><br/>

						2.11. Tecnologias<br/>

						So todas as ferramentas digitais, sistemas, aplicativos, incluindo as de identificao pessoal, como por exemplo sites, formulrios, cadastros digitais, ou ainda o acesso ao Wi-Fi e outros servios oferecidos em razo do uso dos servios operacionais ou administrativos, como cmeras de segurana, realidade aumentada etc.<br/><br/>

						2.12. Cookies<br/>

						So arquivos que armazenam temporariamente, no seu prprio computador ou dispositivo mvel, sobre o que voc est visitando em um determinado site da Internet. Com base nestas informaes, as empresas que utilizam cookies em seus respectivos sites podem aprimorar a experincia de navegao para cada pessoa, por exemplo, oferecendo uma melhor visualizao ou at mesmo oferecendo informaes mais relevantes a voc. Eventualmente poderemos utilizar cookies para melhorar a sua experincia de navegao, e quando isto acontecer voc ser avisado durante a navegao no site, e poder aceitar ou no.<br/><br/>

						3. Quais Dados Pessoais so coletados e de qual forma?<br/>

						Para poder prover nossos servios, ns coletamos seus Dados Pessoais cujo tipo de dado se prope a cumprir um objetivo especfico.<br/>

						A definio de quais dados coletamos, respeita as necessidades mnimas e legtimas de cada servio, que vo desde cadastros, atividades administrativas, financeiras, gerao de relatrios, gerao de informaes para atendimento a rgos pblicos, fiscais, jurdicos, no mbito municipal, estadual e federal.<br/>

						Estes dados podem ser coletados atravs de documentos entregues por voc, como, por exemplo, documentos originais emitidos por outras empresas ou rgos pblicos, cpias impressas de documentos pessoais, informaes suas para contato e emisso de boletos bancrios, entre outros.<br/>

						Tambm, estes dados podem ser coletados por formulrios eletrnicos, sistemas remotos, pginas da Web, ou qualquer outro sistema digital de coleta de dados.<br/><br/>

						4. Por quanto tempo mantemos seus Dados Pessoais?<br/>

						Nosso SGPD  Sistema de Gesto da Proteo de Dados possui regras especficas sobre a coleta, armazenamento, tratamento, manuteno e descarte de Dados Pessoais, bem como o perodo de reteno adequado para cada tipo de Dado Pessoal coletado, considerando a necessidade, finalidade ou para o cumprimento de obrigaes legais, regulatrias, contratuais, entre outras, sempre fundamentadas em bases legais.<br/>

						A manuteno, seja de um documento impresso ou digital, depende de cada atividade em que este deve ser tratado, sendo que em alguns casos as leis relacionadas podem nos obrigar a manter dados de forma perene.<br/>

						Tambm, dados pessoais podero ser mantidos sob nossa custdia por tempo indeterminado, armazenados em sistemas apropriados, e seguindo todos os critrios de Segurana da Informao, nas seguintes circunstncias:<br/><br/>

						a) para o cumprimento de novas obrigaes legais em fase de elaborao;<br/>
						b) para atender aos critrios de conformidade constantes em nosso Sistema de Gesto de Segurana da Informao;<br/>
						c) para combater aes de fraudes ou corrupo;<br/>
						d) para assegurar outras situaes no previstas e que julgarmos serem de interesse do Titular a recuperao dos seus dados pessoais, sendo assim legtimo interesse.<br/><br/>

						5. Com quem compartilhamos seus Dados Pessoais?<br/>

						Para que possamos prestar os nossos servios, utilizamos parceiros credenciados que podero tratar parcialmente seus Dados Pessoais, limitados a finalidade legtima e mnimo necessrio. Tambm, nestes casos, adotamos polticas que exigem destes parceiros prticas de segurana e privacidade adequadas para o tratamento dos Dados Pessoais.<br/><br/>

						So considerados nossos parceiros de negcio, ou de apoio aos nossos servios, e que podero tratar seus dados pessoais:<br/>

						a) empresas coligadas do grupo;<br/>
						b) imobilirias e corretores de imveis;<br/>
						c) instituies financeiras;<br/>
						d) escritrios jurdicos ou empresas de cobrana;<br/>
						e) outras empresas coligadas  BARBO, como por exemplo uma SPE (Sociedade de Propsito Especfico), cuja finalidade  a gesto dos empreendimentos imobilirios.<br/><br/>

						Tambm, para cumprirmos a lei, poder ser necessrio compartilhar seus Dados Pessoais com autoridades pblicas, mas sempre defenderemos os seus direitos como Titular.<br/>

						 importante salientar que no compartilhamos seus Dados Pessoais com fabricantes de mveis, fornecedores de eletrodomsticos, fornecedores ou prestadores de servios em geral.<br/><br/>

						6. Quais so os seus direitos como Titular?<br/>

						Voc poder a qualquer momento:<br/><br/>

						6. Quais so os seus direitos como Titular?<br/><br/>

						6.1. Obter a Confirmao e Acesso a uma cpia dos seus Dados Pessoais existentes em nossos sistemas, bem com solicitar informaes sobre com quem Compartilhamos seus Dados Pessoais.<br/><br/>

						6.2. Solicitar Correo ou Atualizao dos Dados Pessoais, de forma a mant-los precisos.<br/><br/>

						6.3. Solicitar a Eliminao de Dados Pessoais que no sejam mais necessrios para o cumprimento dos servios contratados conosco ou para o cumprimento de questes legais. Em certos casos, alguns dados podero ser Anonimizados, ou seja, sero alterados de tal forma que no permitam sua identificao. Em outros casos, informaremos sobre a impossibilidade de eliminao dos dados pessoais em razo de cumprimento de obrigao legal, ou para cumprir um dos tpicos listados em Por quanto tempo mantemos seus Dados Pessoais?.<br/><br/>

						6.4. Solicitar Portabilidade dos seus Dados Pessoais para outros prestadores de servios semelhantes, na forma definida no contrato de prestao de servios.<br/><br/>

						6.5. No Consentir que seus Dados Pessoais sejam utilizados. Sempre que necessrio, ns solicitaremos o seu consentimento de forma clara e objetiva e voc ser livre para consentir ou no com o uso dos seus Dados Pessoais. No consentindo e sendo o Dado Pessoal necessrio para a realizao de um servio, ficaremos limitados e no poderemos atender  sua necessidade.<br/><br/>

						6.6. Revogar (retirar) um consentimento realizado anteriormente. Contudo, nos casos de revogao pelo Titular ou de seu responsvel legal,  possvel que tenhamos que cancelar os servios contratados ou limitar nossos servios e experincias.<br/><br/>

						6.7. Voc ainda poder Opor-se ao tratamento de algum de seus Dados Pessoais desde que estes dados no atendam a um legtimo interesse ou no atendam a alguma necessidade legal ou contratual.<br/><br/>

						7. Quais so nossas responsabilidades e como protegemos seus Dados Pessoais?<br/><br/>

						Na condio de Controlador, ns cuidamos dos seus Dados Pessoais com responsabilidade e utilizamos somente para fins de prestao dos nossos Servios. Alm desta poltica, adotamos conceituadas tcnicas e boas prticas de segurana e governana, incluindo o uso de criptografia e outros sistemas de preveno de incidentes, aes educativas, mecanismos de superviso, monitoramento e procedimentos para mitigar riscos e outros aspectos relacionados  Segurana da Informao, tudo com o objetivo de proteger seus Dados Pessoais.<br/>

						Embora todas estas aes, a segurana da informao no depende exclusivamente de nossas aes. Por exemplo, acesso ou uso no autorizado das suas contas em servios diversos de Internet, falhas dos dispositivos, programas e antivrus desatualizados, os quais no esto sob o nosso controle, entre outros fatores, podem comprometer a segurana dos seus Dados Pessoais.<br/>

						Portanto, a sua atuao preventiva, postura cuidadosa e confidencialidade dos logins e senhas, tambm  de extrema importncia para manter em um ambiente seguro.<br/><br/>

						8. Como falar sobre seus Dados Pessoais com a gente?<br/>

						Se voc acredita que seus Dados Pessoais foram utilizados de forma incompatvel com esta Poltica de Privacidade ou com a Lei Geral de Proteo de Dados Pessoais, ou mesmo se voc tiver dvidas sobre o tratamento de seus Dados Pessoais ou exerccio de seus direitos, bem como comentrios e sugestes sobre esta poltica, entre em contato conosco.<br/><br/>

						 Encarregado (DPO): MODUS DPO - Flvio Ibelli Callegari<br/>
						 E-mail: dpo@modusdpo.com.br<br/>
						 Endereo: MODUS DPO - Rua do Rosrio, 1524, Cj 08, Centro, Piracicaba  SP 13400-186 A/c DPO | (19) 2538-9460<br/><br/>

						9. Atualizao e mudanas nesta Poltica de Privacidade.<br/>

						Como estamos em constante processo de aperfeioamento dos nossos servios, nossa Poltica de Privacidade tambm ser aperfeioada, sempre objetivando atender s novas necessidades, aos requisitos no tratamento de dados pessoais, alteraes legais ou novas exigncias ou regulamentaes da ANPD.<br/><br/>

						10. Aplicao<br/>

						Esta Poltica de Privacidade se aplica s seguintes empresas: BARBO RIO CLARO EMPREENDIMENTOS LTDA, pessoa jurdica de direito privado, CNPJ/MF sob n 11.004.164/0001-03, Rua 09 CJ, n 500, Sala 02, Cidade Jardim, Rio Claro/SP, CEP 13501-100; e EMPREENDIMENTOS BARBO LTDA, pessoa jurdica de direito privado, CNPJ/MF sob n 00.567.422/0001-43, Rua 09 CJ, n 500, Sala 01, Cidade Jardim, Rio Claro/SP, CEP 13501-100<br/>

						<br /><br />

						<button onClick={() => dispatch(Actions.close_modal('politica'))}
							className='col-12 text-center'>Fechar
						</button>

					</Content>
				</Modal.Body>
			</Modal>
		</>
	);
}
