import React, { useEffect, useState } from "react";
import Content from "./style";
import ScrollableAnchor from "react-scrollable-anchor";

import Fade from "react-reveal/Fade";

import imposto from "images/blog/declarar/declarar_imovel.jpeg";
import plantas from "images/blog/beneficios/beneficios_apartamento.jpg";

import CardBeneficios from "components/cardBlog_beneficios";
import CardImposto from "components/cardBlog_imposto";

function ArtigoBeneficios(props) {

  const [isMobile, setMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth >= 992) {
      setMobile(false);
    } else {
      setMobile(true);
    }
  }, [window.innerWidth]);

  return (
    <ScrollableAnchor id="">
      <Content className="col-12 row justify-content-center">

        <Fade left>
          <div className="left-side my-5">
            {/* {isMobile ? ""
              : */}
            <div className=""></div>
            {/* } */}

            <div className="flex m-4">
              <span className="title">Iluminao para cada ambiente</span><p />
              <span className="texto">
                Com a pandemia passamos muito mais tempo dentro de casa, <br />
                e isso nos mostrou a necessidade de criarmos
                <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> ambientes mais<br />
                  confortveis</a>. Entretanto, ao contrrio do que voc pode pensar,<br />
                conforto no  apenas ter um sof fofinho, mas detalhes que <br />
                proporcionem um espao mais acolhedor, incluindo a iluminao. <br />
                Porm, a iluminao vai das lmpadas, refletores, lustres, pendentes, <br />
                abajures, spots e arandelas.<br /><br />


                Leve em conta o aspecto funcional do cmodo e descubra qual  o <br />
                tipo de lmpada, cor da parede, portas, janelas e disposio dos<br />
                mveis para criar o efeito de iluminao que voc deseja. <br />
                Em ambientes como
                <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> salas de estar, jantar e quartos </a>, o ideal so <br />
                as luzes amarelas e iluminao suave, algo pontual e aconchegante. <br />
                J em ambientes que exigem concentrao, como escritrios, <br />
                cozinhas e salas de estudo, aposte em luz direta, ampla, branca e <br />
                abundante, elas ajudam a manter o estado de alerta e garante um <br />
                detalhamento maior da viso. J quando falamos nas cores de cada<br />
                cmodo, as cores mais neutras como branco, off white, cinza e bege <br />
                so as preferidas para compor ambientes mais iluminados. Da mesma <br />
                forma, cores mais quentes como a terracota, do a sensao de <br />
                acolhimento e suavidade para o ambiente. Outra forma de garantir <br />
                mais conforto e iluminao para cada espao  investir em portas e <br />
                janelas amplas, integrar a sala de estar com a varanda e usar cortinas<br />
                mais delicadas. Dessa forma, voc vai aproveitar a luz do dia e um <br />
                ambiente ainda mais claro. Agora  s se colocar a mo na massa e <br />
                deixar o <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> seu apartamento </a>
                com a iluminao ideal em cada ambiente!
              </span>
            </div>
          </div>
        </Fade>
        <Fade right>
          <div className="image-side">
          </div>
        </Fade>
        {isMobile ?
          <Fade>
            <div className="box-artigo">
              <span className="title">VEJA MAIS</span><p />
              <div className="maquinas">
                <CardImposto image={imposto}
                   items="Postado em Dicas - 01/03/2023"
                   title="Declarar Imposto"
                   text="Declarar imvel financiado no imposto de renda" />
              </div>
              <div className="maquinas pb-5">
                <CardBeneficios image={plantas}
                  items="Postado em Dicas - 01/03/2023"
                  title="Benefcios das Plantas"
                  text="Leve a natureza de forma consciente para o design do seu lar" />
              </div>
            </div>
          </Fade>
          :
          <Fade>
            <div className="box-artigo">
              <span className="title">VEJA MAIS</span><p />
              <div className="maquinas">
                <CardImposto image={imposto}
                  items="Postado em Dicas - 01/03/2023"
                  title="Declarar Imposto"
                  text="Declarar imvel financiado no imposto de renda" />
                <CardBeneficios image={plantas}
                  items="Postado em Dicas - 01/03/2023"
                  title="Benefcios das Plantas"
                  text="Leve a natureza de forma consciente para o design do seu lar" />
              </div>
            </div>
          </Fade>
        }
      </Content>
    </ScrollableAnchor >
  );
}

export default ArtigoBeneficios;
