import React, { useEffect, useState } from "react";
import Content from "./style";
import ScrollableAnchor from "react-scrollable-anchor";

import Fade from "react-reveal/Fade";

import iluminacao from "images/blog/iluminacao/iluminacao_casa.jpeg";
import plantas from "images/blog/beneficios/beneficios_apartamento.jpg";

import CardIluminacao from "components/cardBlog_iluminacao";
import CardBeneficios from "components/cardBlog_beneficios";
function ArtigoImposto(props) {

  const [isMobile, setMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth >= 992) {
      setMobile(false);
    } else {
      setMobile(true);
    }
  }, [window.innerWidth]);

  return (
    <ScrollableAnchor id="">
      <Content className="col-12 row justify-content-center">

        <Fade left>
          <div className="left-side my-5">
            {isMobile ?
              <div className="flex m-0">
                <span className="title">Declarar imvel financiado <br />no imposto de renda</span><p />
                <span className="texto">
                  Imposto de renda  uma tarefa muito importante e que pode ser muito <br />
                  complicada, ainda mais quando voc<br />
                  precisa declarar um
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> imvel financiado. </a><br />
                  Duas coisas que voc precisa saber: <br />
                  a primeira  que um
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> financiamento <br />
                    imobilirio </a> no deve ser declarado <br />
                  como uma dvida, a segunda  que voc<br />
                  no deve colocar o valor total do
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> financiamento </a>, pois esse valor pode <br />
                  mudar. Voc precisa declarar somente <br />
                  o valor pago at o momento, isso inclui <br />
                  a entrada, as prestaes j quitadas, <br />
                  os impostos, custos de corretagem, e <br />
                  por a vai, mas nunca as parcelas que <br />
                  ainda sero pagas no futuro. Para <br />
                  facilitar a sua declarao preste aten-<br />
                  o: Imveis devem ser discriminados <br />
                  na ficha de Bens e Direitos.
                  <a href='/barbo/empreendimentos_interna' target="_blank"> Os aparta- <br />
                    mentos </a>ficam na linha 11, casas na linha <br />
                  12 e terrenos na linha 13. No esquea<br />
                  de fornecer o mximo de informaes. <br />
                  O endereo, a inscrio municipal e a <br />
                  rea total do terreno tm um espao <br />
                  especfico para preenchimento. O pre-<br />
                  enchimento do campo Situao em <br />
                  31/12/2022 funciona assim: se o imvel <br />
                  foi financiado em 2023 preencha esse <br />
                  campo com R$ 0. Se foi financiado<br />
                  antes de 2023 permanece o mesmo <br />
                  valor declarado no incio de 2023. <br />
                  J no campo Situao em 31/12/2023, <br />
                  se o imvel foi financiado em 2023 <br />
                  coloque o valor da entrada mais as <br />
                  parcelas j pagas. Se financiou antes<br />
                  de 2023, voc precisa somar o valor <br />
                  declarado em 2022 com as parcelas <br />
                  adicionais que foram pagas.

                </span>
              </div>
              :
              <div className="flex m-4">
                <span className="title">Declarar imvel financiado no imposto de renda</span><p />
                <span className="texto">
                  Imposto de renda  uma tarefa muito importante e que pode ser muito <br />
                  complicada, ainda mais quando voc precisa declarar um
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> imvel financiado. <br /></a>

                  Duas coisas que voc precisa saber: a primeira  que um
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> financiamento <br /> imobilirio </a>

                  no deve ser declarado como uma dvida, a segunda  que <br />
                  voc no deve colocar o valor total do
                  <a href='https://www.instagram.com/barboempreendimentos/' target="_blank"> financiamento </a>

                  , pois esse valor pode mudar.<br />
                  Voc precisa declarar somente o valor pago at o momento, isso inclui <br />
                  a entrada, as prestaes j quitadas, os impostos, custos de corretagem, <br />
                  e por a vai, mas nunca as parcelas que ainda sero pagas no futuro.<br />
                  Para facilitar a sua declarao preste ateno: Imveis devem ser discriminados <br />
                  na ficha de Bens e Direitos.
                  <a href='/barbo/empreendimentos_interna' target="_blank"> Os apartamentos </a>

                  ficam na linha 11, casas na linha <br />
                  12 e terrenos na linha 13. No esquea de fornecer o mximo de informaes. <br />
                  O endereo, a inscrio municipal e a rea total do terreno tm um espao <br />
                  especfico para preenchimento.<br />
                  O preenchimento do campo Situao em 31/12/2022 funciona assim: <br />
                  se o imvel foi financiado em 2023 preencha esse campo com R$ 0. <br />
                  Se foi financiado antes de 2023 permanece o mesmo valor declarado <br />
                  no incio de 2023. J no campo Situao em 31/12/2023, se o imvel <br />
                  foi financiado em 2023 coloque o valor da entrada mais as parcelas j pagas. <br />
                  Se financiou antes de 2023, voc precisa somar o valor declarado em 2022 <br />
                  com as parcelas adicionais que foram pagas.

                </span>
              </div>
            }
          </div>
        </Fade>
        <Fade right>
          <div className="image-side">
          </div>
        </Fade>
        {isMobile ?
          <Fade>
            <div className="box-artigo">
              <span className="title">VEJA MAIS</span><p />
              <div className="maquinas">
                <CardIluminacao image={iluminacao}
                  items="Postado em Dicas - 01/03/2023"
                  title="Iluminao"
                  text="Para dar um toque mais aconchegante e requintado para os seus espaos" />
              </div>
              <div className="maquinas pb-5">
                <CardBeneficios image={plantas}
                  items="Postado em Dicas - 01/03/2023"
                  title="Benefcios das Plantas"
                  text="Leve a natureza de forma consciente para o design do seu lar" />
              </div>
            </div>
          </Fade>
          :
          <Fade>
            <div className="box-artigo">
              <span className="title">VEJA MAIS</span><p />
              <div className="maquinas">
                <CardIluminacao image={iluminacao}
                  items="Postado em Dicas - 01/03/2023"
                  title="Iluminao"
                  text="Para dar um toque mais aconchegante e requintado para os seus espaos" />
                <CardBeneficios image={plantas}
                  items="Postado em Dicas - 01/03/2023"
                  title="Benefcios das Plantas"
                  text="Leve a natureza de forma consciente para o design do seu lar" />
              </div>
            </div>
          </Fade>
        }
      </Content>
    </ScrollableAnchor>
  );
}

export default ArtigoImposto;
